@import url('slider/swiper-bundle.css')

$bg-main-color: #EDE1DE

@font-face
    font-family: 'Raleway'
    src: url('../fonts/Raleway/static/Raleway-Regular.ttf')

@font-face
    font-family: 'Roboto'
    src: url('../fonts/Roboto/Roboto-Regular.ttf')

*,
*:before,
*:after
    margin: 0
    padding: 0
    box-sizing: border-box
    font-weight: normal

ol,
ul
    list-style: none

a
    text-decoration: none

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration
    display: none

body
    font-family: 'Raleway',
    font-style: normal

body::-webkit-scrollbar
    width: 10px
    background-color: #f9f9fd

body::-webkit-scrollbar-thumb
    border-radius: 10px
    background-color: #87957E

body::-webkit-scrollbar-track
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2)
    border-radius: 10px
    background-color: #f9f9fd

.title
    font-weight: 400
    font-size: 32px
    line-height: 20px
    color: #3A4736

.container
    max-width: 1210px
    margin: 0 auto
.header-main
    background-color: $bg-main-color
    min-width: 320px // 1140px ?
    position: sticky
    top: 0
    z-index: 100
    width: 100%
    // border: 1px solid #87957E
    // border-radius: 6px
    .header__container
        display: flex
        align-items: center
        justify-content: space-between

        .header__left-block
            display: flex
            align-items: center
            .main-logo__img
                width: 111px
                height: 51px
                padding: 3px 0px 0px

            .left-block__nav-head
                margin-left: 69px

                .nav-head__ul
                    display: flex

                    .nav-head__li:nth-child(1)
                        margin-right: 40px
                    .nav-head__li:nth-child(2)
                        margin-right: 40px
                    .nav-head__li:nth-child(3)
                        margin-right: 40px
                    .nav-head__btn
                        color: #3A4736
                        font-weight: 400
                        font-size: 20px
                        line-height: 20px

        .header__right-block
            display: flex
            align-items: center

            .right-block__search-head
                display: flex
                align-items: center
                position: relative
                margin-right: 20px

                .search-head__input
                    border: 1px solid #afaf9ea0
                    border-radius: 6px
                    background-color: $bg-main-color
                    width: 176px
                    height: 40px
                    padding: 8px
                    font-weight: 400
                    font-size: 16px
                    line-height: 24px
                    position: absolute
                    right: 0px
                    z-index: 12
                    transition: 0.7s
                    &::placeholder
                        font-weight: 400
                        font-size: 16px
                        line-height: 20px
                    &:focus
                        width: 380px

                .search-head__btn
                    position: absolute
                    right: 11px
                    z-index: 13
                    .btn-head__img
                        fill: #87957E
                        &:hover
                            fill: black

            .right-block__btn-head
                display: flex
                align-items: center

                .btn-head__heart
                    margin-right: 22px

                .heart__btn
                    width: 26px
                    height: 23px
                    fill: none
                    &:hover
                        fill: #87957E

                .person__btn
                    width: 26px
                    height: 23px
                    fill: none
                    stroke: black
                    &:hover
                        fill: #87957E
                        stroke: #87957E
.slider
    background-color: $bg-main-color
    .itcss
        position: relative
        padding-bottom: 50px
    .itcss__wrapper
        position: relative
        overflow: hidden
        background-color: #eee
        .itcss__items
            display: flex
            transform-style: preserve-3d
            backface-visibility: hidden
            transition: transform 1.5s ease
            will-change: transform

            .itcss__item
                position: relative
                flex: 0 0 100%
                max-width: 100%
                transform-style: preserve-3d
                backface-visibility: hidden
                will-change: transform
                .container
                    max-width: 1000px
                    margin: 0 auto
                .itcss__img
                    width: 100%
                    height: 100%
                    object-fit: cover
                .slide__text
                    font-weight: 400
                    font-size: 24px
                    line-height: 23px
                    color: #3A4736
                    margin-top: 18px

    .itcss__indicators
        position: absolute
        right: 0
        bottom: 10px
        left: 0
        display: flex
        justify-content: center
        align-items: center
        margin: 0 15%
        padding-left: 0
        list-style: none
        user-select: none

        .itcss__indicator
            flex: 0 1 auto
            width: 12px
            height: 12px
            margin-right: 5px
            margin-left: 5px
            background-color: #87957e3d
            background-clip: padding-box
            border-top: 10px solid transparent
            border-bottom: 10px solid transparent
            cursor: pointer
            box-sizing: initial
            border-radius: 50%
        .itcss__indicator_active
            width: 56px
            height: 0px
            background: #87957E
            border-radius: 85.8369px
        .transition-none
            transition: none
    .slider__call
        position: absolute
        top: 0
        .call__title
            font-weight: 500
            font-size: 40px
            line-height: 45px
            text-indent: 3px
            color: #3A4736
            margin-top: 81px
        .call__slide-2
            margin-top: 55px

        .call__btn
            width: 250px
            height: 50px
            background: #87957E
            border: 1px solid #3A4736
            box-shadow: 0px 0px 5px #87957E, 0px 0px 10px #B1BEA8
            border-radius: 6px
            margin-top: 49px
            font-weight: 400
            font-size: 20px
            line-height: 20px
            color: #EEE4E2
            cursor: pointer

//  Interior  ////////////////

.interior
    background-color: $bg-main-color
    min-width: 320px
    // padding-bottom: 57px
    // border: 1px solid #3A4736
    // border-radius: 6px 6px 6px 6px
    // border-top: none

    .interior__title
        margin-bottom: 30px
        padding-top: 34px

    .interior__cards
        display: grid
        grid-template-columns: 1fr 1fr 1fr
        grid-template-rows: 320px 320px
        gap: 28px 30px
        grid-template-areas: "Area1 Area2 Area3" "Area4 Area4 Area3"
        // padding-bottom: 60px

        .img-div
            width: 100%
            height: 100%

            .interior__img
                width: 100%
                height: 100%
                object-fit: cover
                border: 1px solid #87957E
                border-bottom: none
                border-radius: 6px 6px 0px 0px

        .interior__caption
            background-color: white
            display: flex
            justify-content: center
            align-items: center
            background: #FDFDFD
            border: 1px solid #87957E
            border-top: none
            border-radius: 0px 0px 6px 6px
            height: 50px
            .interior__text
                font-weight: 400
                font-size: 20px
                line-height: 20px
                color: #3A4736

        .interior__card:nth-child(1)
            grid-area: Area1
            margin-bottom: 50px

        .interior__card:nth-child(4)
            grid-area: Area4
            margin-bottom: 50px

        .interior__card:nth-child(2)
            grid-area: Area2
            margin-bottom: 50px

        .interior__card:nth-child(3)
            grid-area: Area3
            margin-bottom: 50px
            // width: 100%
            // height: 100%

.articles
    background-color: $bg-main-color
    min-width: 320px
    // padding-top: 135px
    padding-bottom: 44px
    position: relative

    .articles__title
        padding-top: 60px
        // margin-bottom: 30px

    .swiper
        padding-right: 2px
        padding-left: 2px

    .swiper-slide
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25)
        border: 1px solid #87957E
        border-radius: 6px
        transition: .3s
        height: 404px
        overflow: hidden
        display: inline-block
        position: relative
        &:hover
            transform-origin: right
            transform: scale(1.04)
        .article-like
            width: 43px
            height: 41px
            background: rgba(217, 217, 217, 0.6)
            border-radius: 6px
            display: flex
            align-items: center
            justify-content: center
            position: absolute
            top: 11px
            right: 12px
        .article-like > svg
            &:hover
                fill: rgba(194, 35, 35, 0.6)
                stroke: red

    .swiper-slide-active:hover
        transform-origin: 0% 50%
        transform: scale(1.04)

    .swiper-slide-next:hover
        transform-origin: 0% 50%
        transform: scale(1.04)

    .article-title_div
        display: flex
        justify-content: center
        height: 104px

        .article-title
            max-width: 240px
            font-weight: 400
            font-size: 16px
            line-height: 20px
            color: #3A4736
            text-align: center
            -webkit-line-clamp: 3
            display: -webkit-box
            -webkit-box-orient: vertical
            overflow: hidden

    .swiper-slide img
        height: 300px
        width: 100%
        object-fit: cover
        border-radius: 6px 6px 0px 0px
        margin-bottom: 20px
        border-bottom: 1px solid #87957E

    .swiper-pagination
        // display: flex
        // justify-content: center
        position: inherit !important
        // width: 100% !important
        padding-top: 40px

        .swiper-pagination-bullet
            background: #87957E
            width: 19px !important
            height: 19px !important
            border: 2px solid #3A4736
            border-radius: 85.8369px
        .swiper-pagination-bullet-active
            width: 53px !important
            height: 19px !important
            background: #87957E
            border: none
            border-radius: 85.8369px
    .articles__background
        width: 100%
        min-height: 113px
        background: #87957E
        border: 1px solid #3A4736
        border-radius: 6px
        position: absolute
        top: 257px

    .articles__showMore
        display: flex
        align-items: center
        justify-content: right
        .articles__btn
            font-weight: 500
            font-size: 24px
            line-height: 20px
            color: #3A4736
            transform: rotate(0.15deg)
            padding-right: 12px
            cursor: pointer

.about-plants
    background: #FFFFFF
    // border: 1px solid #3A4736
    // border-radius: 6px
    min-width: 320px
    .about-plants__title
        padding-top: 60px
    .swiper
        padding-right: 5px
        padding-left: 5px
        padding-top: 30px
        max-width: 900px
        margin: 0 auto

        .swiper-slide
            box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25)
            border: 1px solid #87957E
            border-radius: 6px
            transition: .3s
            min-height: 430px
        .about-plants__like
            width: 43px
            height: 41px
            background: rgba(217, 217, 217, 0.6)
            border-radius: 6px
            display: flex
            align-items: center
            justify-content: center
            position: absolute
            top: 11px
            right: 12px
        .about-plants__like > svg
            &:hover
                fill: rgba(209, 187, 187, 0.989)
                stroke: red

        .swiper-slide-active:hover
            transform-origin: 0% 50%
            transform: scale(1.09)

        .swiper-slide-next:hover
            transform: scale(1.09)

        .swiper-slide-next ~ .swiper-slide
            &:hover
                transform-origin: right
                transform: scale(1.09)

        .about-plants__card_img img
            height: 350px
            width: 100%
            object-fit: cover
            border-radius: 6px 6px 0px 0px
            margin-bottom: 20px
            border-bottom: 1px solid #87957E
        .swiper-pagination
            position: inherit !important
            padding-top: 35px

            .swiper-pagination-bullet
                background: #87957E
                width: 19px !important
                height: 19px !important
                border: 2px solid #3A4736
                border-radius: 85.8369px
            .swiper-pagination-bullet-active
                width: 53px !important
                height: 19px !important
                background: #87957E
                border: none
                border-radius: 85.8369px

        .about-plants-title_div
            display: flex
            justify-content: center

        .about-plants-text
            max-width: 240px
            font-weight: 400
            font-size: 16px
            line-height: 20px
            color: #3A4736
            text-align: center
            -webkit-line-clamp: 3
            display: -webkit-box
            -webkit-box-orient: vertical
            overflow: hidden

    .articles__showMore
        display: flex
        align-items: center
        justify-content: right
        padding-bottom: 52px
        .articles__btn
            font-weight: 500
            font-size: 24px
            line-height: 20px
            color: #3A4736
            transform: rotate(0.15deg)
            padding-right: 12px
            cursor: pointer

.community
    background-color: $bg-main-color
    min-width: 320px
    // padding-top: 66px
    padding-bottom: 60px

    .community__title-background
        background: #87957E
        border: 1px solid #3A4736
        border-radius: 6px
        margin-bottom: 30px
        .community__title-wrapper
            min-height: 80px
            display: flex
            align-items: center
        .title-background__text
            color: #3A4736

    .community__cards
        display: flex
        justify-content: center
        .community__card-first
            margin-right: 15px
            .about-person__img
                width: 80px
                height: 80px
                border-radius: 50%
                background: url('../img/community/avatar_1.jpg') center no-repeat
                background-size: cover
                // background-position: center
                // object-fit: cover
                margin-left: 19px
        .community__card-second
            margin-left: 15px
            .about-person__img
                width: 80px
                height: 80px
                border-radius: 50%
                background: url('../img/community/avatar_2.jpg') center no-repeat
                background-size: cover
                margin-left: 19px
        .community__card
            position: relative
            min-width: 484px
            min-height: 532px
            // margin: 0 auto
            background: #FFFFFF
            border: 1px solid #3A4736
            overflow: hidden
            border-radius: 6px
            // display: flex
            .community__card-background
                position: absolute
                height: 235.05px
                left: 10px
                right: -80px
                top: -80px
                background: #87957E
                transform: matrix(0.97, 0.26, -0.49, 0.87, 0, 0)
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)

            .card__about-person
                position: absolute
                right: 34px
                top: 34px
                text-align: right
                display: flex
                .about-person__name
                    font-weight: 400
                    font-size: 20px
                    line-height: 20px
                    color: #FFFFFF
                    margin-bottom: 1px
                .about-person__status
                    font-weight: 400
                    font-size: 12px
                    line-height: 20px
                    color: #F2D4C2
                    margin-bottom: 1px
                    font-family: 'Roboto'
                .about-person__rating
                    display: flex
                    justify-content: right
                    align-items: center
                    p
                        font-style: normal
                        font-weight: 500
                        font-size: 17px
                        line-height: 14px
                        color: #FFFFFF
                        margin-left: 4px
                        font-family: 'Roboto'

            .community__card-logos
                display: flex
                align-items: center
                position: absolute
                top: 110px
                right: 33px
                margin-top: 20px
                svg
                    margin-left: 21px
        .community__card-main-text
            position: absolute
            top: 200px
            left: 34px
            right: 34px
            bottom: 81px
            overflow: scroll
            scroll: hidden
            &::-webkit-scrollbar
                width: 0
            h2
                margin-bottom: 50px
                font-weight: 500
                font-size: 24px
                line-height: 20px
                color: #3A4736
            p
                font-weight: 400
                font-size: 16px
                line-height: 20px
                color: #3A4736

        .card__read-more
            position: absolute
            height: 55px
            width: 100%
            border-top: 1px solid #3A4736
            box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25)
            bottom: 0
            display: flex
            justify-content: center
            align-items: center
            p
                font-weight: 400
                font-size: 20px
                line-height: 20px
                color: #3A4736
    .btn-wrapper
        margin-top: 63px
        display: flex
        justify-content: center
        .community__btn
            width: 260px
            height: 57px
            background: #87957E
            border: 1px solid #3A4736
            border-radius: 6px
            font-style: normal
            font-weight: 400
            font-size: 20px
            line-height: 20px
            color: #EEE4E2

    .community__hr
        margin: 71px auto 0
        width: 500px
        height: 0px
        border: 3px solid #3A4736

.main-page__footer
    min-width: 320px
    background: #525D4D
    border-top: 1px solid #3A4736
    //border-radius: 6px
    .footer__container
        display: flex
        justify-content: space-between
        align-items: center
        padding-top: 67px
        padding-bottom: 52px
        .footer__logo
            margin-right: 8px
            padding-top: 10px
        .footer__nav
            display: flex
            ul:nth-child(1)
                margin-right: 30px
            ul:nth-child(2)
                margin-right: 30px
            ul:nth-child(3)
                margin-right: 30px
            a
                font-weight: 400
                font-size: 20px
                line-height: 20px
                color: #EDE1DE
            .nal_li_first, .nal_li_second
                padding-bottom: 20px

        .footer__btn
            svg:nth-child(1)
                margin-right: 20px
            svg:nth-child(2)
                margin-right: 20px
            svg:nth-child(3)
                margin-right: 20px

