}
.about-plants .swiper .swiper-pagination .swiper-pagination-bullet {
  background: #87957E;
  width: 19px !important;
  height: 19px !important;
  border: 2px solid #3A4736;
  border-radius: 85.8369px;
}
.about-plants .swiper .swiper-pagination .swiper-pagination-bullet-active {
  width: 53px !important;
  height: 19px !important;
  background: #87957E;
  border: none;
  border-radius: 85.8369px;
}
.about-plants .swiper .about-plants-title_div {
  display: flex;
  justify-content: center;
}
.about-plants .swiper .about-plants-text {
  max-width: 240px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #3A4736;
  text-align: center;
  -webkit-line-clamp: 3;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.about-plants .articles__showMore {
  display: flex;
  align-items: center;
  justify-content: right;
  padding-bottom: 52px;
}
.about-plants .articles__showMore .articles__btn {
  font-weight: 500;
  font-size: 24px;
  line-height: 20px;
  color: #3A4736;
  transform: rotate(0.15deg);
  padding-right: 12px;
  cursor: pointer;
}

